.main-section {
  background-color: none;
  display: -ms-flexbox;
  display: flex;
  height: 100vh;
  -ms-flex-pack: center;
      justify-content: center;
}

.content {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  letter-spacing: 1.4px;
  line-height: 1.5;
  padding: 0 50px;
  width: 50%;
}

.latest, .experience {
  background-color: #000;
  color: #FFF;
}

.seen {
  background-color: #FFF;
  color: #000;
}

.content h2 {
  font-size: 5vw;
  line-height: 1.2;
  margin-bottom: 0;
}

.content p {
  font-size: 1.8vw;
}

.main-section-image {
  height: 100%;
  width: 50%;
}

.artists .main-section-image {
  -o-object-fit: cover;
     object-fit: cover;
}

.seen img{
  -o-object-fit: contain;
     object-fit: contain;
}

.experience img {
  -o-object-fit: cover;
     object-fit: cover;
}



