.artist-profile {
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(255,255,255,0.5)), to(rgba(0,0,0,0.8)));
  background: -webkit-linear-gradient(rgba(255,255,255,0.5), rgba(0,0,0,0.8));
  background: -o-linear-gradient(rgba(255,255,255,0.5), rgba(0,0,0,0.8));
  background: linear-gradient(rgba(255,255,255,0.5), rgba(0,0,0,0.8));
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  -ms-flex-direction: column;
      flex-direction: column;
  top: 8vh;
  height: 92vh;
  width: 100%;
}

.artist-info {
  background-image: url('../assets/profile-background.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  -webkit-box-shadow: 0px 2px 15px 0px rgba(0,0,0,1);
          box-shadow: 0px 2px 15px 0px rgba(0,0,0,1);
  display: -ms-flexbox;
  display: flex;
  font-family: 'Samsung Sharp Sans Regular Regular';
  height: calc(100%/3);
  padding-top: 3px;
  width: 100%;
}

.artist-user,
.artist-bio,
.artist-count {
  -ms-flex-align: center;
      align-items: center;
  background-color: rgba(0, 0, 0, 0.7);
  color: #FFF;
  display: -ms-flexbox;
  display: flex;
  height: 100%;
  -ms-flex-pack: center;
      justify-content: center;
  width: calc(100%/3);
}

.artist-user {
  display: -ms-flexbox;
  display: flex;
  font-family: 'SamsungSharpSans-Bold';
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  padding: 5px;
}

.artist-user p {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  font-size: 20px;
  -ms-flex-pack: center;
      justify-content: center;
  margin: 0;
  height: auto;
  width: 100%;
}

.artist-bio {
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
  text-align: justify;
  padding: 20px;
}

.artist-bio button {
  border: 2px solid black;
  font-size: 24px;
  width: 100%;
}

.artist-bio button:hover {
  border: 2px solid white;
  color: white;
}

.artist-bio span, .artist-count {
  font-family: 'SamsungSharpSans-Bold';
  font-size: 20px;
}

.artist-count {
  -ms-flex-direction: column;
      flex-direction: column;
}

.count-title {
  margin-bottom: 0;
  letter-spacing: 1.1px;
}

.count {
  margin: 0 0 5px 0;
  font-size: 50px;
}

.artist-tag {
  -webkit-box-shadow: 0px 0px 4px 0px #000;
          box-shadow: 0px 0px 4px 0px #000;
  height: 60%;
  -o-object-fit: fill;
     object-fit: fill;
  width: 80%;
}

.artist-profile-images {
  display: grid;
  grid-template-columns: calc(100%/3) calc(100%/3) calc(100%/3);
  grid-template-rows: auto;
  height: auto;
  overflow-y: scroll;
  padding: 25px;
  width: 100%;
}

.profile-imgs {
  height: 300px;
  width: 100%;
  padding: 10px;
}

button {
  border: none;
  background: none;
  outline-style: none;
  cursor: pointer;
}



