nav {
  display: -ms-flexbox;
  display: flex;
  height: 8vh;
  -ms-flex-pack: center;
      justify-content: center;
  position: fixed;
  width: 100%;
  z-index: 100;
}

.search-bar {
  background-color: black;
  border: none;
  border-right: 2px solid white;
  color: white;
  font-size: 24px;
  opacity: .7;
  outline: none;
  padding-left: 10px;
  width: 40%;
}

.nav-section {
  -ms-flex-align: center;
      align-items: center;
  background-color: black;
  border-right: 2px solid white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-positive: 2;
      flex-grow: 2;
  font-size: 24px;
  font-weight: bold;
  -ms-flex-pack: center;
      justify-content: center;
  opacity: 0.7;
  text-decoration: none;
  width: 15%;
}

.link {
  color: white;
  -webkit-transition: .5s;
  -o-transition: .5s;
  transition: .5s;
}

.nav-section:hover .link {
  color: red;
}



