.popup {
  -ms-flex-align: center;
      align-items: center;
  background-color: rgba(0,0,0,0.8);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 100vh;
  -ms-flex-pack: center;
      justify-content: center;
  position: absolute;
  width: 100vw;
}
.popup-txt {
  color: white;
  font-size: 30px;
  margin-bottom: 20px;
}

.popup button {
  border: 2px solid #FFF;
  color: #FFF;
  font-size: 24px;
  padding: 20px;
  width: 250px;
}

.popup button:hover {
  background-color: #FFF;
  color: #000;
}



