.sign-up {
  -ms-flex-align: center;
      align-items: center;
  background-image: url('./assets/sign-up-background.png');
  background-repeat: no-repeat;
  background-size: cover;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 100vh;
  -ms-flex-pack: center;
      justify-content: center;
