.user-info {
  -ms-flex-align: center;
      align-items: center;
  background-color: rgba(0, 0, 0, 0.7);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 65vh;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  width: 60%;
}

.username-container,
.tag-container,
.short-bio-container {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
